<template>
    <div v-if="imgList.length">
        <cube-slide ref="slide" :data="imgList" class="imageswiper" @change="changePage">
            <cube-slide-item v-for="(item, index) in imgList" :key="index" @click.native="clickHandler(item.jump_id, item.book_id)">
                <a href="javascript:;">
                <img :src="item.banner_pic">
                </a>
            </cube-slide-item>
            <template slot="dots" slot-scope="props">
                <span class="my-dot" :class="{active: props.current === index}" v-for="(item, index) in props.dots" :key="index">{{index + 1}}</span>
            </template>
        </cube-slide>
    </div>
    
</template>
<script type="text/ecmascript-6">

export default {
    props: {
        imgList: {
            type: Array,
            default: function(){
                return []
            }
        }
    },
    data() {
        return {
        }
    },
    methods: {
        changePage(current) {
            console.log('当前轮播图序号为:' + current)
        },
        clickHandler(jump, id) {
            if(jump===3) {
                this.$router.push({
                    path: `/book/${id}`
                })
            }
        }
    }
}
</script>
<style lang="stylus" scoped>
    .imageswiper
        width 100%
        height 165px
        background-color #ffffff
        border-bottom 5px solid #f6f7fa
</style>


