<template>
    <div>
        <div class="loading-container" v-show="!channelDetails.length">
            <loading></loading>
        </div>
        <div class="container">
            <div class="header">
                <div class="logo"></div>
                <cube-scroll
                    ref="scroll"
                    :data="channelList"
                    direction="horizontal"
                    class="horizontal-scroll-list-wrap"
                >
                    <ul class="list-wrapper">
                        <li
                            v-for="(tab,index) in channelList"
                            :key="tab.channel_id"
                            class="list-item"
                            :class="currentIdx==index ? 'active' : ''"
                            @click="tapTab(tab.channel_id, index)"
                        >
                            <a href="javascript:;">{{tab.channel_name}}</a>
                        </li>
                    </ul>
                </cube-scroll>
                <div class="tools">
                    <div class="icon search"></div>
                    <div class="icon shelf"></div>
                </div>
            </div>

            <div v-for="(module, index) in channelDetails" :key="index">
                <div v-if="module.type==1">
                    <!-- 广告轮播样式 -->
                    <image-swiper :imgList="module.block.length? module.block[0].book_list: []"></image-swiper>
                </div>
                <div v-else-if="module.type==4">
                    <!-- 横版3封面样式 -->
                    <hori-three :module="module"></hori-three>
                </div>
                <div v-else-if="module.type==12">
                    <!-- 多封面横向滑动样式 -->
                    <hori-scroll :module="module"></hori-scroll>
                </div>
                <div v-else-if="module.type==15">
                    <!-- 竖版多详情样式 -->
                    <vert-three :module="module"></vert-three>
                </div>

                <div v-else-if="module.type==16">
                    <!-- 导航菜单样式 -->
                    <nav-menu :navMenus="module.block.length? module.block[0].book_list: []"></nav-menu>
                </div>
                <div v-else>
                    <!-- 其他样式12/15/4 -->
                    <vert-three :module="module"></vert-three>
                </div>
            </div>

            <div class="footer">
                <ul class="links">
                    <li>
                        <a
                            href="https://static-html.zrjhwenhua.com/jwxs/client/index.html"
                            target="_blank"
                        >客户端</a>
                    </li>
                    <li>
                        <a href="javascript:;" class="grey" target="_blank">触屏版</a>
                    </li>
                    <li>
                        <a href="https://www.jiuwei.com/" target="_blank">电脑版</a>
                    </li>
                </ul>
                <p class="copyright">Copyright © 2019 m.jiuwei.com</p>
            </div>
            <div class="localdown">
                <div class="localdown-app">
                    <div class="localdown-app_logo"></div>
                    <div class="localdown-app_text">
                        <h3>
                            下载
                            <span>九尾小说APP</span>
                        </h3>
                        <p>免费畅读更多好书</p>
                    </div>
                </div>
                <div class="localdown-btn">下载</div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
import Loading from 'components/loading/loading'
import ImageSwiper from 'components/module/image-swiper'
import NavMenu from 'components/module/nav-menu'
import VertThree from 'components/module/vert-three'
import HoriThree from 'components/module/hori-three'
import HoriScroll from 'components/module/hori-scroll'

export default {
    name: 'channel',
    data() {
        return {
            channelList: [],
            channelDetails: [],
            currentIdx: 0,
            currentId: 0
        }
    },
    mounted() {
        this.getBookChannel()
    },
    methods: {
        getBookChannel() {
            this.$post('/user/my_channel').then(res => {
                // console.log(res)
                if (res.code === 10000) {
                    let result = res.result
                    this.channelList = result.default_channel.length
                        ? result.default_channel
                        : result.my_channel
                    this.getChannelDetail(this.channelList[0].channel_id)
                }
            })
        },
        getChannelDetail(id) {
            let data = {
                channel_id: id
            }
            this.$post('/book/city_channel_detail', data).then(res => {
                if (res.code === 10000) {
                    this.channelDetails = res.result
                }
            })
        },
        async tapTab(id, idx) {
            //点击tab-bar
            if (this.currentIdx === idx) {
                return false
            } else {
                this.currentIdx = idx
                this.currentId = id
                this.getChannelDetail(this.currentId)
            }
        }
    },
    components: {
        Loading,
        ImageSwiper,
        NavMenu,
        VertThree,
        HoriThree,
        HoriScroll
    }
}
</script>
