<template>
    <div class="nav-menu" v-if="navMenus.length">
        <ul class="nav">
            <li class="nav-item" v-for="(nav, index) in navMenus" :key="index">
                <a href="javascript:;" @click="jumpTo(nav.jump_id, nav.type_id)">
                    <img :src="nav.banner_pic" />
                    <p>{{nav.book_name}}</p>
                </a>
            </li>
        </ul>
    </div>
</template>
<script type="text/ecmascript-6">
export default {
    name: 'NavMenus',
    props: {
        navMenus: {
            type: Array,
            default: function() {
                return []
            }
        }
    },
    methods: {
        jumpTo(jump_id, type_id) {
            //分类7、排行8、指定榜单9
            let curl =
                jump_id == 7
                    ? '/sort'
                    : jump_id == 8
                    ? '/rank'
                    : jump_id == 9
                    ? '/list/' + type_id
                    : ''
            this.$router.push({
                path: curl
            })
        }
    }
}
</script>
